@font-face {
    font-family: "Roboto-Regular"; /*a name to be used later*/
    src: url("../assets/Roboto-Regular.ttf"); /*URL to font*/
  }
  
  @font-face {
    font-family: "Roboto-Bold"; /*a name to be used later*/
    src: url("../assets/Roboto-Bold.ttf"); /*URL to font*/
  }
  
  .P32 {
    padding: 2rem 1rem 1rem 3rem;
    font-family: Roboto-Regular;
  }
  
  .P32-step{
    width: 80vw;
  }
  .P32-h1 {
    font-size: 1.4rem;
    color: #8473ff;
    margin-top: 0rem;
    margin-bottom: 0.2rem;
  }

.P32-bigger{
  font-size: 2.3rem;
}


  .P32-text {
    padding-right: 2vw;
  }
  
  .P32-img {
    position: absolute;
    bottom: -4rem;
    right: 0rem;
  }

  .P32-imgS{
    position: absolute;
    bottom: -4rem;
    right: 3rem;
  }
  
  .P32-questions-container{
    margin: 0rem 22vw 0px 0rem;
  }
  .P32-question{
    display: flex;
    padding: 0.18rem;
  }
  .P32-question-hover{
    display: flex;
    border: solid 0.2rem #8473ff;
    padding: 0.18rem;
  }
  .P32-label{
    font-size: 1.5rem;
    align-self: center;
    padding-left: 1rem;
  }
  