@font-face {
    font-family: "Roboto-Regular"; /*a name to be used later*/
    src: url("../assets/Roboto-Regular.ttf"); /*URL to font*/
  }
  
  @font-face {
    font-family: "Roboto-Bold"; /*a name to be used later*/
    src: url("../assets/Roboto-Bold.ttf"); /*URL to font*/
  }
  
  .P42 {
    padding: 2rem 1rem 1rem 3rem;
    font-family: Roboto-Regular;
  }
  
  .P42-step{
    width: 80vw;
  }
  .P42-h1 {
    font-size: 1.6rem;
    color: #8473ff;
    margin-top: 1rem;
    margin-bottom: 0.5rem;
  }

.P42-bigger{
    font-size: 2.9rem;
}


  .P42-text {
    padding-right: 5vw;
  }
  
  .P42-img {
    position: absolute;
    bottom: -4rem;
    right: 0rem;
  }

  .P42-imgS{
    position: absolute;
    bottom: -4rem;
    right: 3rem;
  }
  
  .P42-questions-container{
    margin: 0rem 22vw 0px 0rem;
  }
  .P42-question{
    display: flex;
    padding: 0.5rem;
  }
  .P42-question-hover{
    display: flex;
    border: solid 0.2rem #8473ff;
    padding: 0.5rem;
  }
  .P42-label{
    font-size: 1.8rem;
    align-self: center;
    padding-left: 1rem;
  }
  